import React, { useEffect, useState } from 'react';
import ReactDOM from 'react-dom';
import { useFormik } from 'formik';
import * as yup from 'yup';
import { Button, Divider, TextField, Alert, AlertTitle, Snackbar } from '@mui/material';
import { LoadingButton } from '@mui/lab';

import axios from 'api/request';

const validationSchema = yup.object({
    title: yup.string('Enter your title').required('title is required'),
    content: yup.string('Enter your content').required('content is required')
});

const NewArticle = () => {
    const [loading, setLoading] = useState({
        add: false
    });
    const [error, setError] = useState('');
    const [open, setOpen] = React.useState(false);

    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }

        setOpen(false);
    };

    const formik = useFormik({
        initialValues: {
            title: '',
            content: ''
        },
        validationSchema: validationSchema,
        onSubmit: async (values) => {
            if (!loading.add) {
                try {
                    loading.add = true;
                    setLoading(loading);
                    const res = await axios.post('/api/v1/articles', values);
                    loading.add = false;
                    setOpen(true);
                } catch (error) {
                    setOpen(true);
                    setError(error);
                }
            }
        }
    });

    return (
        <div>
            <Snackbar open={open} autoHideDuration={3000} onClose={handleClose} anchorOrigin={{ vertical: 'top', horizontal: 'center' }}>
                <Alert onClose={handleClose} severity={error === '' ? 'success' : 'error'} sx={{ width: '100%' }}>
                    {error === '' ? 'Success ' : error}
                </Alert>
            </Snackbar>
            <form onSubmit={formik.handleSubmit}>
                <TextField
                    fullWidth
                    id="title"
                    name="title"
                    label="title"
                    type="text"
                    value={formik.values.title}
                    onChange={formik.handleChange}
                    error={formik.touched.title && Boolean(formik.errors.title)}
                />
                <TextField
                    fullWidth
                    id="content"
                    name="content"
                    type="text"
                    value={formik.values.content}
                    onChange={formik.handleChange}
                    error={formik.touched.content && Boolean(formik.errors.content)}
                    helperText={formik.touched.content && formik.errors.content}
                    margin="dense"
                    multiline
                    rows="20"
                />
                <LoadingButton color="primary" variant="contained" type="submit" loading={loading.add}>
                    Submit
                </LoadingButton>
            </form>
        </div>
    );
};

export default NewArticle;
